<script>
  import _ from 'lodash'
  import api from 'src/packages/Api'

  import References from 'view/components/references'

  import Loading from 'partials/Loading'
  import Editor from 'view/components/editor'
  import Uploader from 'view/components/uploader'
  import Relateds from 'view/components/relateds'
  import Multiselect from 'view/components/multiselect'
  import MultiTagselect from 'view/components/multitagselect'
  
  export default {
    name: 'update-blog',
    data () {
      return {
        blog: {},
        id: '',
        title: '',
        slug: '',
        description: '',

        keywords: '',
        body: '',

        editor: null,
        
        category: {},
        categories: [],
        references: [],
        related: [],

        tag: [],
        tags: [],

        writer: {},
        writers: [],
        
        loaded: false,
        apiUrl: 'blogs/' + this.$route.params.id + "/upload/images",
        coverApiUrl: 'blogs/' + this.$route.params.id + "/upload/cover",
        imageApiUrl: 'blogs/' + this.$route.params.id + "/upload/image"        
      }
    },

    components: {
      Editor,
      Loading,
      Uploader,
      Relateds,
      References,
      Multiselect,
      MultiTagselect
    },
    async created () {
      try {
        const response = await api.get(`blogs/${this.$route.params.id}`)        

        const res = response.data

        this.blog = res
        this.id = res.id
        this.title = res.title
        this.slug = res.slug
        this.keywords = res.keywords
        this.description = res.description
        this.references = res.references.data        
        this.category = res.category_id ? res.category.data : {}

        if (res.writer_id) {
          this.writer = res.writer.data
          this.writers.push(res.writer.data)
        }        
        
        res.tags.map(t => { this.tag.push({id: t.name, name: t.name}) })

        const related = await api.post('related', res.relateds.data)

        this.related = related.data.map(r => {
          const selector = r.type === 'categories' ? 'name' : 'title'

          const rel = {
            selector,
            id: r.entity,
            type: {id: r.type, name: this.$t(`related.type_${r.type}`)}
          }

          return rel
        })

        this.loaded = true
      } catch(error) {  
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'categories' })
      }
    },
    methods: {
      getImages () {
        const images = this.blog.images.data || []

        return images
      },

      getCover () {
        const cover = this.blog.cover
          ? [this.blog.cover.data] 
          : []

        return cover
      },

      getImage () {
        const image = this.blog.image
          ? [this.blog.image.data] 
          : []

        return image
      },

      reInitImages (response) {
        this.blog = response.data
      },

      findCategories (categories) {
        this.categories = categories
      },
      selectCategory (category) {
        if (category !== undefined) {
          this.category = category
        }        
      },

      findWriter (writers) {
        this.writers = writers
      },
      selectWriter (writer) {
        if (writer !== undefined) {
          this.writer = writer
        }        
      },

      findTag (result) {
        const tags = result.map(tag => { return { id: tag.name, name: tag.name } })

        this.tags = tags
      },
      selectTags (tag) {
        this.tag = tag
      },

      handleEditor (editor) {
        this.editor = editor

        this.editor.setContents(this.blog.body || '')
      },
      deleteRef (reff) {
        this.references = this.references.filter(r => r !== reff)
      },

      changeRelated ({old, changed}) {
        const relates = this.related.map(r => {
          if (r === old) {
            return changed
          } else {
            return r
          }
        })

        this.related = relates
      },

      deleteRelated (rel) {
        this.related = this.related.filter(r => r !== rel)
      },

      update () {

        this.$validator.validateAll().then((result) => {
          const tags = this.tag.map(t => t.name);
          const relations = this.related
            .filter(r => !_.isEmpty(r.id))
            .map(r => {
              return {
                type: r.type.id,
                related_id: r.id.id
              }
            })

          if (result) {
            const data = {
              title: this.title,
              slug: this.slug,
              category_id: _.isEmpty(this.category) ? 0 : this.category.id,
              writer_id: _.isEmpty(this.writer) ? null : this.writer.identifier,
              keywords: this.keywords,
              description: this.description,
              body: this.editor.getContents(),
              references: this.references,
              related: relations,
              tags
            }

            api.put(`blogs/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('blog.updated'), type: 'success' }
              )
              this.$router.push({ name: 'blog', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
	<loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.blogs.update' | translate">
          
          <div class="row">
            <div class="col">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'blog.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('slug')}">
                    <div class="input-group">
                      <input
                        id="slug"
                        name="slug"
                        v-model="slug"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('slug')"></i>
                      <label class="control-label" for="slug">{{'blog.slug'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('slug')"
                             class="help text-danger">{{
                        errors.first('slug')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <multiselect
                    :select="category"
                    :selects="categories"
                    :apiUrl="'categories/search'"
                    :rtl="true" 
                    @search="findCategories"
                    @select="selectCategory"
                  />
                </div>

                <div class="col">
                  <multiselect
                    :select="writer"
                    :selects="writers"
                    :apiUrl="'users/search'"
                    :rtl="true" 
                    @search="findWriter"
                    @select="selectWriter"
                  />
                </div>
              </div>

              <div class="row row-reverse">
                <div class="col">
                  <multi-tagselect
                    :value="[]"
                    :rtl="true"
                    :select="tag"
                    :selects="tags"
                    @search="findTag"
                    @select="selectTags"
                    :apiUrl="'tags/search'"
                    :placeholder="'tag.create' | translate"
                  />
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('keywords')}">
                    <div class="input-group">
                      <input
                        id="keywords"
                        name="keywords"
                        v-model="keywords"
                      />
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('keywords')"></i>
                      <label class="control-label" for="keywords">{{'blog.keywords'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('keywords')"
                             class="help text-danger">{{
                        errors.first('keywords')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>

              <div class="row mt-2">
                <textarea 
                  id="description"
                  class="col-md-12"
                  v-model="description"
                  :placeholder="'blog.description' | translate"
                >
                </textarea>
              </div>

              <div class="row mt-2">
                <div class="col-3">
                  <Uploader
                    :maxFiles="1"
                    :placeholder="'blog.cover' | translate"
                    :images="getCover"
                    :apiUrl="coverApiUrl"
                    @onChange="reInitImages"
                  />
                </div>
                <div class="col-3">
                  <Uploader
                    :maxFiles="1"
                    :placeholder="'blog.image' | translate"
                    :images="getImage"
                    :apiUrl="imageApiUrl"
                    @onChange="reInitImages"
                  />
                </div>
                <div class="col-6">
                  <Uploader
                    :placeholder="'blog.images' | translate"
                    :images="getImages"
                    :apiUrl="apiUrl"
                    @onChange="reInitImages"
                  />
                </div>
              </div>

              <div class="row mt-2">
                <Editor @editor="handleEditor"/>
              </div>

              <div class="row mt-2">
                <div class="col" v-html="blog.old_content"></div>
              </div>
            </div>
          </div>

          <references :references="references" @delRef="deleteRef"/>

          <relateds :relateds="related" @delRel="deleteRelated" @changeRel="changeRelated"/>

          <div class="row mt-5">
            <div class="col justify-content-center text-center">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>

        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
